@font-face {
  font-family: HomeVideo;
  src: url("HomeVideo-BLG6G.ccfc4d92.ttf");
}

@font-face {
  font-family: HomeVideoBold;
  src: url("HomeVideoBold-R90Dv.05c56721.ttf");
}

@font-face {
  font-family: BPdotsSquareVF;
  src: url("BPdotsSquareVF.25fec6ee.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: FunnelDisplay;
  src: url("FunnelDisplay.db902d50.woff2");
}

@font-face {
  font-family: ZxGamut;
  src: url("ZxGamut-Variable.6528b92f.woff2");
  font-display: swap;
}

html, body {
  background-color: #171717;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

a {
  text-align: center;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: HomeVideo;
  font-size: 3vmin;
  position: fixed;
}

a:link {
  color: #0827f5;
}

a:visited {
  color: #f5be07;
}

nav {
  z-index: 9999;
  background: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  position: fixed;
}

.nav-mobile {
  bottom: 5vmin;
}

button {
  text-align: center;
  background: none;
  border: none;
  margin: 0;
  padding: 2vmin;
  font-family: HomeVideo;
  font-size: 4vmin;
}

.vertical-center {
  margin: auto;
  padding: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.horizontal-center {
  margin: auto;
  padding: auto;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.app {
  opacity: 0;
  transition: opacity .2s cubic-bezier(0, .55, .45, 1);
}

.pointer {
  cursor: pointer;
}

.route {
  color: #404040;
  background: none;
  transition: color .3s, text-shadow .8s, box-shadow .8s;
}

.route-mobile {
  font-size: 8vmin;
}

.route:hover {
  color: #ffc000;
  text-shadow: 0 0 16px #ffc000;
}

.route-active {
  color: #faa000;
  transition: color .3s, text-shadow .8s, box-shadow .8s;
}

.route-active:hover {
  color: #faa000;
  text-shadow: 0 0 16px #faa000;
}

.unselectable {
  -webkit-touch-callout: none;
  user-select: none;
  -khtml-user-select: none;
}

.main-paragraph {
  color: #fafafa;
  text-align: left;
  width: 80%;
  margin: 1% 10% 0;
  padding: auto;
  font-family: ZxGamut;
  font-size: 3vmin;
  font-weight: 400;
  position: relative;
}

.text-center {
  text-align: center;
}

.main-paragraph-mobile {
  font-size: 7vmin;
}

h1 {
  color: #fafafa;
  text-align: left;
  width: 80%;
  margin: 0 10%;
  padding: auto;
  font-family: BPdotsSquareVF;
  font-size: 5vmin;
  font-weight: 800;
  position: relative;
}
